<template>
  <div>
    <div class="row text-center" style="margin-top: 15px;" v-if="list.length==0&&loaded">
      <img src="../../assets/less/img/icons/svg/pencils.svg" alt="">
      <p style="margin-top: 15px;"><small>啊哦，你还没有日报哦，快去添加吧~</small></p>
    </div>
    <div class="row text-center" v-if="!loaded">加载中，请稍后...</div>
    <div class="row" v-if="loaded">
      <div class="col-xs-3" v-for="item in list">
        <div class="tile">
          <h3 class="tile-title">{{item.title}}</h3>
          <p class="item-content" v-html="changeContent(item.content)"></p>
          <router-link class="btn btn-primary btn-large btn-block" :to="{ name: 'edit', params: { id: item.id }}">
            <span class="fui-new"></span> 编辑
          </router-link>
          <router-link class="btn btn-info btn-large btn-block" :to="{ name: 'detail', params: { id: item.id }}">
            <span class="fui-link"></span> 查看
          </router-link>
          <a class="btn btn-danger btn-large btn-block" href="javascript:void(0)" @click="deleteItem(item.id)"><span class="fui-trash"></span> 删除</a>
        </div>
      </div>
    </div> <!-- /tiles -->
  </div>
</template>

<script>

import Base from './base.js';

export default {
  name: 'reportIndex',
  data () {
    return {
      list: [],
      auth: '',
      loaded: false
    }
  },
  components:{
  },
  mixins: [Base],
  mounted(){
    this.getList();
  },
  methods: {
    getList() {
      var that = this;
      $.ajax({
        url: '/dayreport/api/getmyreport',
        data: {
          token: that.token
        },
        type: 'POST',
        dataType: 'json',
        success: function(res) {
          that.loaded = true;
          if(res.code != 200){
            that.$wii.toast.show({
              text: res.msg,
              type: 'error',
              time: 1500
            });
          }else{
            that.list = res.data.list;
            that.auth = res.data.auth;
          }
        }
      })
    },
    deleteItem(id) {
      var that = this;
      $.ajax({
        url: '/dayreport/api/deletereport',
        data: {
          token: that.token,
          id: id
        },
        type: 'POST',
        dataType: 'json',
        success: function(res) {
          if(res.code != 200){
            that.$wii.toast.show({
              text: res.msg,
              type: 'error',
              time: 1500
            });
          }else{
            that.getList();
          }
        }
      })
    },
    changeContent(content){
      content = JSON.parse(content);
      var info = '';
      for (let i in content){
        info += content[i].item_title + content[i].item_content + '。';
      }
      return info;
    }
  }
}
</script>
<style lang="less" scoped>
.item-content{
  margin-top: 10px;
  display: inline-block;
  height: 72px;
  word-break:break-all;
  display:-webkit-box;
  -webkit-line-clamp:3;
  -webkit-box-orient:vertical;
  overflow:hidden;
}
